import {mapState} from "vuex"
import uniCountdown from '@/components/uni-countdown/uni-countdown.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
import { getToken } from '@/utils/auth'
export default {
	data() {
		return {
			title: 'Hello',
			hour: 0,
			minute: 0,
			second: 0,
			isOver:false,
		};
	},
	onLoad() {},
	onShow() {
		this.countTime();
	},
	computed: {
		...mapState(['userName'])
	},
	methods: {
		goHuiZis() {
			uni.navigateTo({
				url: '/pages/huiHzs/index'
			});
		},
		goMyItem(){
			if( getToken() ){
				uni.navigateTo({
					url: '/pages/myItem/index'
				});
			}
			else {
				this.$refs.popup.open();
			}
		},
		competition(){
			uni.navigateTo({
				url: '/pages/competition/index'
			});
		},
		toRanking(){
			uni.navigateTo({
				url: '/pages/ranking/index'
			});
		},
		isCompetition(){
			this.isOver = true ;
		},
		countTime() {
			//获取当前时间
			let date = new Date();
			let now = date.getTime();
			//设置截止时间
			let str = date.getFullYear() + '/' + (Number(date.getMonth()) + 1) + '/' + date.getDate() + ' 22:00:00';
			let endDate = new Date(str);
			let end = endDate.getTime();
			//时间差
			let leftTime = end - now;
			//定义变量 d,h,m,s保存倒计时的时间
			if (leftTime >= 0) {
				this.hour = Math.floor((leftTime / 1000 / 60 / 60) % 24);
				this.minute = Math.floor((leftTime / 1000 / 60) % 60);
				this.second = Math.floor((leftTime / 1000) % 60);
			}
			if( date.getHours() >= 22){
				this.isOver = true ;
			}
		},
		close(done){
			
			done()
		},
		confirm(done){
			uni.navigateTo({
				url: '/pages/login/index?from=home'
			});
			done()
		}

	},
	components: {
		uniCountdown,
		uniPopup,
		uniPopupDialog
	}
};
